<template>
  <div>
    <div class="hotopct">
      <div class="hotxia"></div>
      <div class="date">更新日期:{{ date }}</div>
    </div>
    <ul>
      <HotList
        v-for="(item, index) in List"
        :key="item.id"
        :item="item"
        :index="index"
        @change-current-play-song="
          $emit('change-current-play-song', $event);
          $emit('change-current-play-song-list', List);
        "
      />
    </ul>
  </div>
</template>
<script>
import HotList from "@/components/HotList.vue";
export default {
  data() {
    return {
      List: [],
      date: String,
    };
  },
  components: {
    HotList,
  },
  methods: {},
  created() {
    this.axios
      .get("https://music.kele8.cn/top/list?idx=1")
      .then((response) => {
        // console.log(response.data.playlist.tracks);
        this.List = response.data.playlist.tracks;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  mounted() {
    var myDate = new Date();
    this.date =
      (myDate.getMonth() + 1 < 10
        ? "0" + (myDate.getMonth() + 1)
        : myDate.getMonth() + 1) +
      "月" +
      myDate.getDate() +
      "日";
  },
};
</script>

